.App {
  text-align: center;
  /* background-color: #08c708; */
  /* width: 100%; */
  overflow-x: auto;
  overflow-y: hidden;
  white-space: nowrap;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #09d3ac;
}

.about {
  margin-top: 10%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
}

.stat {
  font-size: 17px;
  text-align: left;
}
.name {
  font-size: 13px;
  font-weight: 600;
  text-align: left;
  color: #3b4241;
}

.run {
  width: 10px;
  height: 10px;
  border-radius: 50%;
  background-color: rgb(26, 202, 26);
  display: inline-block;
  margin-left: 5px;
}
table {
  font-family: Verdana;
  font-size: 14px;
  border-collapse: collapse;
  /* width: 100%; */
  color: rgb(104, 92, 92);
}

td,
th {
  padding: 10px;
  text-align: left;
  margin: 0;
  border-bottom: 1px solid rgb(230, 227, 227);
  font-size: 12px;
}
.dev {
  width: "30%";
}
.hori {
  overflow-x: hidden;
  overflow-y: hidden;
  white-space: nowrap;
}
.tables {
  width: 67%;
  height: 100%;
}
.tables1 {
  width: 100%;
  height: 100%;
}
