@media screen and (max-width: 600px) {
  .nas {
    display: none;
  }
  .status {
    width: 100% !important;
    margin: auto;
  }
  .TableContainer {
    width: 100vw !important;
  }
  .tables {
    width: 85vw !important;
    height: 100%;
  }
  .tables1 {
    width: 85vw !important;
    height: 100%;
  }
}
